import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBAnimator } from './pb/decentraland/sdk/components/animator.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1042

/**
 * @internal
 */
export const AnimatorSchema: ISchema<PBAnimator> = {
  serialize(value: PBAnimator, builder: ByteBuffer): void {
    const writer = PBAnimator.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBAnimator {
    return PBAnimator.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBAnimator {
    // TODO: this is a hack.
    return PBAnimator.decode(new Uint8Array())
  }
}
