import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBRaycast } from './pb/decentraland/sdk/components/raycast.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1067

/**
 * @internal
 */
export const RaycastSchema: ISchema<PBRaycast> = {
  serialize(value: PBRaycast, builder: ByteBuffer): void {
    const writer = PBRaycast.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBRaycast {
    return PBRaycast.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBRaycast {
    // TODO: this is a hack.
    return PBRaycast.decode(new Uint8Array())
  }
}
