import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBRaycastResult } from './pb/decentraland/sdk/components/raycast_result.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1068

/**
 * @internal
 */
export const RaycastResultSchema: ISchema<PBRaycastResult> = {
  serialize(value: PBRaycastResult, builder: ByteBuffer): void {
    const writer = PBRaycastResult.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBRaycastResult {
    return PBRaycastResult.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBRaycastResult {
    // TODO: this is a hack.
    return PBRaycastResult.decode(new Uint8Array())
  }
}
