import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBAvatarShape } from './pb/decentraland/sdk/components/avatar_shape.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1080

/**
 * @internal
 */
export const AvatarShapeSchema: ISchema<PBAvatarShape> = {
  serialize(value: PBAvatarShape, builder: ByteBuffer): void {
    const writer = PBAvatarShape.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBAvatarShape {
    return PBAvatarShape.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBAvatarShape {
    // TODO: this is a hack.
    return PBAvatarShape.decode(new Uint8Array())
  }
}
