import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBPointerLock } from './pb/decentraland/sdk/components/pointer_lock.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1074

/**
 * @internal
 */
export const PointerLockSchema: ISchema<PBPointerLock> = {
  serialize(value: PBPointerLock, builder: ByteBuffer): void {
    const writer = PBPointerLock.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBPointerLock {
    return PBPointerLock.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBPointerLock {
    // TODO: this is a hack.
    return PBPointerLock.decode(new Uint8Array())
  }
}
