import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBTextShape } from './pb/decentraland/sdk/components/text_shape.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1030

/**
 * @internal
 */
export const TextShapeSchema: ISchema<PBTextShape> = {
  serialize(value: PBTextShape, builder: ByteBuffer): void {
    const writer = PBTextShape.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBTextShape {
    return PBTextShape.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBTextShape {
    // TODO: this is a hack.
    return PBTextShape.decode(new Uint8Array())
  }
}
