import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBMeshCollider } from './pb/decentraland/sdk/components/mesh_collider.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1019

/**
 * @internal
 */
export const MeshColliderSchema: ISchema<PBMeshCollider> = {
  serialize(value: PBMeshCollider, builder: ByteBuffer): void {
    const writer = PBMeshCollider.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBMeshCollider {
    return PBMeshCollider.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBMeshCollider {
    // TODO: this is a hack.
    return PBMeshCollider.decode(new Uint8Array())
  }
}
