import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBUiText } from './pb/decentraland/sdk/components/ui_text.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1052

/**
 * @internal
 */
export const UiTextSchema: ISchema<PBUiText> = {
  serialize(value: PBUiText, builder: ByteBuffer): void {
    const writer = PBUiText.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBUiText {
    return PBUiText.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBUiText {
    // TODO: this is a hack.
    return PBUiText.decode(new Uint8Array())
  }
}
