import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBAudioStream } from './pb/decentraland/sdk/components/audio_stream.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1021

/**
 * @internal
 */
export const AudioStreamSchema: ISchema<PBAudioStream> = {
  serialize(value: PBAudioStream, builder: ByteBuffer): void {
    const writer = PBAudioStream.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBAudioStream {
    return PBAudioStream.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBAudioStream {
    // TODO: this is a hack.
    return PBAudioStream.decode(new Uint8Array())
  }
}
