import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBUiTransform } from './pb/decentraland/sdk/components/ui_transform.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1050

/**
 * @internal
 */
export const UiTransformSchema: ISchema<PBUiTransform> = {
  serialize(value: PBUiTransform, builder: ByteBuffer): void {
    const writer = PBUiTransform.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBUiTransform {
    return PBUiTransform.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBUiTransform {
    // TODO: this is a hack.
    return PBUiTransform.decode(new Uint8Array())
  }
}
