import type { IEngine } from '../../engine/types'
import * as TransformSchema from '../legacy/Transform'
import * as AnimatorSchema from './Animator.gen'
import * as AudioSourceSchema from './AudioSource.gen'
import * as AudioStreamSchema from './AudioStream.gen'
import * as AvatarAttachSchema from './AvatarAttach.gen'
import * as AvatarModifierAreaSchema from './AvatarModifierArea.gen'
import * as AvatarShapeSchema from './AvatarShape.gen'
import * as BillboardSchema from './Billboard.gen'
import * as CameraModeSchema from './CameraMode.gen'
import * as CameraModeAreaSchema from './CameraModeArea.gen'
import * as GltfContainerSchema from './GltfContainer.gen'
import * as MaterialSchema from './Material.gen'
import * as MeshColliderSchema from './MeshCollider.gen'
import * as MeshRendererSchema from './MeshRenderer.gen'
import * as NftShapeSchema from './NftShape.gen'
import * as PointerEventsResultSchema from './PointerEventsResult.gen'
import * as PointerHoverFeedbackSchema from './PointerHoverFeedback.gen'
import * as PointerLockSchema from './PointerLock.gen'
import * as RaycastSchema from './Raycast.gen'
import * as RaycastResultSchema from './RaycastResult.gen'
import * as TextShapeSchema from './TextShape.gen'
import * as UiBackgroundSchema from './UiBackground.gen'
import * as UiTextSchema from './UiText.gen'
import * as UiTransformSchema from './UiTransform.gen'
import * as VisibilityComponentSchema from './VisibilityComponent.gen'

export function defineLibraryComponents({
  defineComponentFromSchema
}: Pick<IEngine, 'defineComponentFromSchema'>) {
  return {
		Animator: defineComponentFromSchema(AnimatorSchema.AnimatorSchema, AnimatorSchema.COMPONENT_ID),
		AudioSource: defineComponentFromSchema(AudioSourceSchema.AudioSourceSchema, AudioSourceSchema.COMPONENT_ID),
		AudioStream: defineComponentFromSchema(AudioStreamSchema.AudioStreamSchema, AudioStreamSchema.COMPONENT_ID),
		AvatarAttach: defineComponentFromSchema(AvatarAttachSchema.AvatarAttachSchema, AvatarAttachSchema.COMPONENT_ID),
		AvatarModifierArea: defineComponentFromSchema(AvatarModifierAreaSchema.AvatarModifierAreaSchema, AvatarModifierAreaSchema.COMPONENT_ID),
		AvatarShape: defineComponentFromSchema(AvatarShapeSchema.AvatarShapeSchema, AvatarShapeSchema.COMPONENT_ID),
		Billboard: defineComponentFromSchema(BillboardSchema.BillboardSchema, BillboardSchema.COMPONENT_ID),
		CameraMode: defineComponentFromSchema(CameraModeSchema.CameraModeSchema, CameraModeSchema.COMPONENT_ID),
		CameraModeArea: defineComponentFromSchema(CameraModeAreaSchema.CameraModeAreaSchema, CameraModeAreaSchema.COMPONENT_ID),
		GltfContainer: defineComponentFromSchema(GltfContainerSchema.GltfContainerSchema, GltfContainerSchema.COMPONENT_ID),
		Material: defineComponentFromSchema(MaterialSchema.MaterialSchema, MaterialSchema.COMPONENT_ID),
		MeshCollider: defineComponentFromSchema(MeshColliderSchema.MeshColliderSchema, MeshColliderSchema.COMPONENT_ID),
		MeshRenderer: defineComponentFromSchema(MeshRendererSchema.MeshRendererSchema, MeshRendererSchema.COMPONENT_ID),
		NftShape: defineComponentFromSchema(NftShapeSchema.NftShapeSchema, NftShapeSchema.COMPONENT_ID),
		PointerEventsResult: defineComponentFromSchema(PointerEventsResultSchema.PointerEventsResultSchema, PointerEventsResultSchema.COMPONENT_ID),
		PointerHoverFeedback: defineComponentFromSchema(PointerHoverFeedbackSchema.PointerHoverFeedbackSchema, PointerHoverFeedbackSchema.COMPONENT_ID),
		PointerLock: defineComponentFromSchema(PointerLockSchema.PointerLockSchema, PointerLockSchema.COMPONENT_ID),
		Raycast: defineComponentFromSchema(RaycastSchema.RaycastSchema, RaycastSchema.COMPONENT_ID),
		RaycastResult: defineComponentFromSchema(RaycastResultSchema.RaycastResultSchema, RaycastResultSchema.COMPONENT_ID),
		TextShape: defineComponentFromSchema(TextShapeSchema.TextShapeSchema, TextShapeSchema.COMPONENT_ID),
		UiBackground: defineComponentFromSchema(UiBackgroundSchema.UiBackgroundSchema, UiBackgroundSchema.COMPONENT_ID),
		UiText: defineComponentFromSchema(UiTextSchema.UiTextSchema, UiTextSchema.COMPONENT_ID),
		UiTransform: defineComponentFromSchema(UiTransformSchema.UiTransformSchema, UiTransformSchema.COMPONENT_ID),
		VisibilityComponent: defineComponentFromSchema(VisibilityComponentSchema.VisibilityComponentSchema, VisibilityComponentSchema.COMPONENT_ID),
  }
}
