import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBCameraMode } from './pb/decentraland/sdk/components/camera_mode.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1072

/**
 * @internal
 */
export const CameraModeSchema: ISchema<PBCameraMode> = {
  serialize(value: PBCameraMode, builder: ByteBuffer): void {
    const writer = PBCameraMode.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBCameraMode {
    return PBCameraMode.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBCameraMode {
    // TODO: this is a hack.
    return PBCameraMode.decode(new Uint8Array())
  }
}
