import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBCameraModeArea } from './pb/decentraland/sdk/components/camera_mode_area.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1071

/**
 * @internal
 */
export const CameraModeAreaSchema: ISchema<PBCameraModeArea> = {
  serialize(value: PBCameraModeArea, builder: ByteBuffer): void {
    const writer = PBCameraModeArea.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBCameraModeArea {
    return PBCameraModeArea.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBCameraModeArea {
    // TODO: this is a hack.
    return PBCameraModeArea.decode(new Uint8Array())
  }
}
