import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBAudioSource } from './pb/decentraland/sdk/components/audio_source.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1020

/**
 * @internal
 */
export const AudioSourceSchema: ISchema<PBAudioSource> = {
  serialize(value: PBAudioSource, builder: ByteBuffer): void {
    const writer = PBAudioSource.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBAudioSource {
    return PBAudioSource.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBAudioSource {
    // TODO: this is a hack.
    return PBAudioSource.decode(new Uint8Array())
  }
}
