import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBAvatarAttach } from './pb/decentraland/sdk/components/avatar_attach.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1073

/**
 * @internal
 */
export const AvatarAttachSchema: ISchema<PBAvatarAttach> = {
  serialize(value: PBAvatarAttach, builder: ByteBuffer): void {
    const writer = PBAvatarAttach.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBAvatarAttach {
    return PBAvatarAttach.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBAvatarAttach {
    // TODO: this is a hack.
    return PBAvatarAttach.decode(new Uint8Array())
  }
}
