import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBVisibilityComponent } from './pb/decentraland/sdk/components/visibility_component.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1081

/**
 * @internal
 */
export const VisibilityComponentSchema: ISchema<PBVisibilityComponent> = {
  serialize(value: PBVisibilityComponent, builder: ByteBuffer): void {
    const writer = PBVisibilityComponent.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBVisibilityComponent {
    return PBVisibilityComponent.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBVisibilityComponent {
    // TODO: this is a hack.
    return PBVisibilityComponent.decode(new Uint8Array())
  }
}
