import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBUiBackground } from './pb/decentraland/sdk/components/ui_background.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1053

/**
 * @internal
 */
export const UiBackgroundSchema: ISchema<PBUiBackground> = {
  serialize(value: PBUiBackground, builder: ByteBuffer): void {
    const writer = PBUiBackground.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBUiBackground {
    return PBUiBackground.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBUiBackground {
    // TODO: this is a hack.
    return PBUiBackground.decode(new Uint8Array())
  }
}
