import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBPointerHoverFeedback } from './pb/decentraland/sdk/components/pointer_hover_feedback.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1062

/**
 * @internal
 */
export const PointerHoverFeedbackSchema: ISchema<PBPointerHoverFeedback> = {
  serialize(value: PBPointerHoverFeedback, builder: ByteBuffer): void {
    const writer = PBPointerHoverFeedback.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBPointerHoverFeedback {
    return PBPointerHoverFeedback.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBPointerHoverFeedback {
    // TODO: this is a hack.
    return PBPointerHoverFeedback.decode(new Uint8Array())
  }
}
