import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBMaterial } from './pb/decentraland/sdk/components/material.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1017

/**
 * @internal
 */
export const MaterialSchema: ISchema<PBMaterial> = {
  serialize(value: PBMaterial, builder: ByteBuffer): void {
    const writer = PBMaterial.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBMaterial {
    return PBMaterial.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBMaterial {
    // TODO: this is a hack.
    return PBMaterial.decode(new Uint8Array())
  }
}
