import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBGltfContainer } from './pb/decentraland/sdk/components/gltf_container.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1041

/**
 * @internal
 */
export const GltfContainerSchema: ISchema<PBGltfContainer> = {
  serialize(value: PBGltfContainer, builder: ByteBuffer): void {
    const writer = PBGltfContainer.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBGltfContainer {
    return PBGltfContainer.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBGltfContainer {
    // TODO: this is a hack.
    return PBGltfContainer.decode(new Uint8Array())
  }
}
