import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBMeshRenderer } from './pb/decentraland/sdk/components/mesh_renderer.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1018

/**
 * @internal
 */
export const MeshRendererSchema: ISchema<PBMeshRenderer> = {
  serialize(value: PBMeshRenderer, builder: ByteBuffer): void {
    const writer = PBMeshRenderer.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBMeshRenderer {
    return PBMeshRenderer.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBMeshRenderer {
    // TODO: this is a hack.
    return PBMeshRenderer.decode(new Uint8Array())
  }
}
