import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBAvatarModifierArea } from './pb/decentraland/sdk/components/avatar_modifier_area.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1070

/**
 * @internal
 */
export const AvatarModifierAreaSchema: ISchema<PBAvatarModifierArea> = {
  serialize(value: PBAvatarModifierArea, builder: ByteBuffer): void {
    const writer = PBAvatarModifierArea.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBAvatarModifierArea {
    return PBAvatarModifierArea.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBAvatarModifierArea {
    // TODO: this is a hack.
    return PBAvatarModifierArea.decode(new Uint8Array())
  }
}
