import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBPointerEventsResult } from './pb/decentraland/sdk/components/pointer_events_result.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1063

/**
 * @internal
 */
export const PointerEventsResultSchema: ISchema<PBPointerEventsResult> = {
  serialize(value: PBPointerEventsResult, builder: ByteBuffer): void {
    const writer = PBPointerEventsResult.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBPointerEventsResult {
    return PBPointerEventsResult.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBPointerEventsResult {
    // TODO: this is a hack.
    return PBPointerEventsResult.decode(new Uint8Array())
  }
}
