import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBBillboard } from './pb/decentraland/sdk/components/billboard.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1090

/**
 * @internal
 */
export const BillboardSchema: ISchema<PBBillboard> = {
  serialize(value: PBBillboard, builder: ByteBuffer): void {
    const writer = PBBillboard.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBBillboard {
    return PBBillboard.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBBillboard {
    // TODO: this is a hack.
    return PBBillboard.decode(new Uint8Array())
  }
}
